.dictionary-selector-modal {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  height: 100%;
  -webkit-transition: top 0.3s ease;
  -o-transition: top 0.3s ease;
  transition: top 0.3s ease;
  background-color: rgba(0, 0, 0, 0.8);
}

.dictionary-selector-modal-header {
  height: 44px;
  width: 100%;
}

.dictionary-selector-modal-header-text {
  height: 44px;
  width: 100%;
  font-size: 17px;
  line-height: 44px;
  text-align: center;
}

.dictionary-selector-modal-header-cancel {
  position: absolute;
  top: 0;
  right: 0;
  height: 44px;
  padding: 0 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 17px;
  cursor: pointer;
}

.dictionary-selector-modal-header-cancel:hover,
.dictionary-selector-modal-header-cancel:focus {
  background-color: rgba(255, 255, 255, 0.4);
}

.dictionary-selector-modal-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  color: #fff;
  cursor: pointer;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.dictionary-selector-modal-item:hover {
  background-color: rgba(255,255,255,0.5);
}


